import { useState } from 'react';
import HomeCanvas from '../components/HomeCanvas';
import HomeTextBox from '../components/HomeTextBox';
import HomeNextBtn from '../components/HomeNextBtn';
import styled from 'styled-components';

export default function HomePage() {
  const [isTextVisible, setIsTextVisible] = useState(false);
  return (
    <Wrap>
      <ContentBox>
        <HomeTextBox isTextVisible={isTextVisible} />
        <HomeNextBtn isVisible={isTextVisible} />
      </ContentBox>
      <MobileContentBox>
        <PolyganImgBox>
          <PolyganImg src="/assets/images/polygan.png" />
        </PolyganImgBox>
        <MobileTextBox>
          <p>  ?</p>
          <p>      ,     .</p>
          <p>        .</p>
          <p>     ,    </p>
          <MobileTitle>
            Frontend Developer <Strong></Strong>
            .
          </MobileTitle>
        </MobileTextBox>
      </MobileContentBox>
      <HomeCanvas setTextVisible={setIsTextVisible} />
    </Wrap>
  );
}

const Wrap = styled.div`
  display: flex;
  width: 100%;
  height: 100vh;
  justify-content: center;
  @media (max-width: 1080px) {
    padding: 0 3.2rem;
  }
`;

const ContentBox = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 6.4rem 0 12.8rem;

  @media (max-width: 1080px) {
    display: none;
  }
`;

const MobileContentBox = styled(ContentBox)`
  display: none;
  padding: 3.2rem 0;
  gap: 3.2rem;
  @media (max-width: 1080px) {
    display: block;
  }
`;

const MobileTextBox = styled.div`
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
  font-size: 1.6rem;
`;

const PolyganImg = styled.img`
  transform-origin: center center;
  width: 80%;
`;

const PolyganImgBox = styled.div`
  aspect-ratio: 1/1;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const MobileTitle = styled.h1`
  font-size: 2.4rem;
`;

const Strong = styled.strong`
  color: #5383e8;
  font-size: 2.8rem;
`;
